.home{
  height:100%;
  width:100vw;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top:5rem;
}
.Typewriter{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  color:#8A817C;
  text-align: center;
}
.image{
  padding: 1rem;
  display: flex;
  width:40vw;
  justify-content: center;
  margin-top:2rem;
}

.button-container{
  margin-top: 5rem;
  display: flex;
  justify-content: space-evenly;
  flex-direction: row;
  align-items: center;

}

.link-button{
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 0.15rem;
  display: flex;
  margin:1rem;
  padding:0.75rem;
  border-radius: 1rem;
  transition: all 0.3s ease-out;
}
.link-button.projects{
  background-color: #8A817C;
  color:#F4F3EE;
  border: 0.15rem solid #8A817C;
}
.link-button.projects:hover{
  color: #cf9c8d;
  background-color: transparent;
}
.link-button.about-me{
  border: 0.15rem solid #8A817C;
  background-color: transparent;
  transition: all 0.3s ease-out;
  color: #cf9c8d; 
}
.link-button.about-me:hover{
  color: #F4F3EE;
  background-color: #8A817C;
}
@media(max-width: 1024px){
  .image{
  width:80vw;
  }
}

@media(max-width: 576px){
  .image{
  width:90vw;
  }
}

